














export default {
	data() {
		return {
			
		};
	},
	onNavigationBarButtonTap(e) {
		//根据 e.index 判断具体某个按钮被点击
		if(e.index === 0){
			uni.showToast({
				title: "您点击了分享按钮" ,
				icon: "none"
			});
		}else if(e.index === 1){
			uni.showToast({
				title: "您点击了提交按钮" ,
				icon: "none"
			});
		}
		
	},
}
